import { has } from '../helpers'

const PLACEHOLDERIMAGE="data:image/png;base64,"
const imageBlock = {
    title: 'Image',
    description: 'A simple image block.',

    default_values: {
        settings: {}
    },
    config: {
        preview (self, callback) {
            if (has(self.settings, 'image')) {
                callback('<img src="' + self.settings.image.url + '" class="img-fluid" />')
            } else {
                callback('<center><img width="100px" src="'+PLACEHOLDERIMAGE+'" class="img-fluid" /></center>')
            }
        },
        styles: [
            {id: 'default', name: 'Default'}
        ]
    }
}

imageBlock.config.getImages =  (apiUrl, searchText='', page=1, callback) => {
    if (apiUrl) {
        fetch(apiUrl+'?page='+page+'&q='+searchText, {credentials: 'include'})
        .then(response => response.json())
        .then(data => {
            if (callback) {
                callback(data)
            }
        })
    } else {
        throw Error('Image Block is not configured properly. Please give valid apiUrl')
    }
}
imageBlock.settings_schema = {
    fields: [
        {
            type: "imageChooser",
            label: "Choose Image",
            model: "image",
            required: true
        }
    ]
}

export default imageBlock